<template>
  <div class="wrap">
    <div class="shop">
      <em class="gray"></em>></div>
    <div class="content">
      <h3></h3>
      <div class="line"></div>
      <ul class="content_info">
        <li class="site">
          <span>&nbsp;&nbsp;</span>
          <el-input v-model="input" placeholder="" style="width:384px"></el-input>
          <!-- <span class="shopInfo"></span> -->
        </li>
        <li class="shopName">
          <span>&nbsp;&nbsp;</span>
          <el-input v-model="input1" placeholder="" style="width:384px"></el-input>
        </li>
        <!-- <li class="wangwang">
          <span>ID&nbsp;&nbsp;</span>
          <el-input v-model="input2" placeholder="" style="width:384px"></el-input>
        </li> -->
        <li class="type">
          <span>&nbsp;&nbsp;</span>
          <el-select v-model="value" placeholder="" @change="valueChange">
            <el-option v-for="item in shopType" :key="item.value" :label="item.className" :value="item">
            </el-option>
          </el-select>
        </li>
        <li class="pic">
          <span class="pic_admin"></span>
          <el-upload class="avatar-uploader" :show-file-list="false" :http-request="uploadImg" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload" ref="upload" action="">
            <img v-if="imageUrl" :src="imageUrl" class="avatar" width="182px" height="182" style="border-right:1px solid #D4D5D8; border-bottom:1px solid #D4D5D8">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </li>
        <li class="addContent" v-for="(item,index) in addArr" :key='index'>
          <i class="el-icon-delete" style="float:right;font-size:20px;cursor:pointer" @click="remove(index)"></i>
          <div>:
            <span>{{item.itemCode+' '+item.itemCity+' '+item.itemZone}}</span>
          </div>
          <div style="margin-top:10px">:
            <span>{{item.jieName}}</span>
          </div>
          <div style="margin-top:10px">:
            <span>{{item.phone}}</span>
          </div>
          <div class="line"></div>
        </li>
        <li class="pullDown" v-show="pull">
          <el-form>
            <el-form-item label="">
              <el-select v-model="itemCode" placeholder="" @change="provinceChange">
                <el-option v-for="(item,index) in provinces" :key="index" :label="item.name" :value="item"></el-option>
              </el-select>
              <el-select v-model="itemCity" placeholder="" style="margin-left:12px;margin-right:12px" @change="cityChange">
                <el-option v-for="(item,index) in city" :key="index" :label="item.name" :value="item"></el-option>
              </el-select>
              <el-select v-model="itemZone" placeholder="" @change="zoneChange">
                <el-option v-for="(item,index) in zone" :key="index" :label="item.name" :value="item"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="jieName" style="width:384px"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="phone" style="width:384px"></el-input>
            </el-form-item>
          </el-form>
          <button class="btn" @click="save"></button>
        </li>
        <li class="addAddress">
          <i class="el-icon-circle-plus" style="color:rgba(23,159,255,1);cursor:pointer" @click="add"> </i>
        </li>
        <li>
          <div class="line"></div>
          <p class="person"></p>
        </li>
        <li class="personInfo">
          &nbsp;&nbsp;
          <el-input v-model="input4" placeholder="" style="width:384px"></el-input><br>
          <span style="margin-left:-10px">&nbsp;&nbsp;</span>
          <el-input v-model="input5" placeholder="" style="width:384px;margin-top:20px"></el-input><br>
          <span>QQ&nbsp;&nbsp;</span>
          <el-input v-model="input6" placeholder="" style="width:384px;margin-top:20px"></el-input>
        </li>
        <li>
          <button class=" btn" style="margin-bottom:60px" @click="addSure"></button>
        </li>
      </ul>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import { mapGetters, mapActions } from 'vuex'
import { getImgSize, uploadFile, uploadPromise } from '../../assets/js/upload'
export default {
  name: 'shopAdmin',
  data () {
    return {
      // 
      disable: false,
      // 
      active: true,
      isCanUpload: false,
      shopType: [],
      provinces: [],
      itemCode: null,
      city: [],
      itemCity: null,
      zone: [],
      itemZone: null,
      provincesCode: '',
      cityCode: '',
      zoneCode: '',
      jieName: '',
      phone: '',
      input: '',
      input1: '',
      input2: '',
      input4: '',
      input5: '',
      input6: '',
      value: '',
      className: '',
      valueCode: '',
      imageUrl: '',
      pull: true,
      addArr: [],
      addContent: false
    }
  },
  watch: {
    isCanUpload (val) {
      if (val) {
        this.$refs.upload.submit()
      }
    }
  },
  computed: {
    ...mapGetters([
      'userInfo'
    ])
  },
  created () {
    this.shopFirst()
    this.Provinces()
  },
  methods: {
    handleAvatarSuccess (res, file) {
      // this.imageUrl = URL.createObjectURL(file.raw)
    },
    uploadImg (img) {
      if (!this.isCanUpload) {
        return false
      }
      uploadPromise.then((res) => {
        if (res.statusText === 'OK') {
          uploadFile(res.data, img.file).then((res) => {
            // this.imageUrl = res.url
            this.imageUrl = res
          }).catch(() => {
            this.$message.error('')
          })
        }
      }).catch(() => {
        this.$message.error('')
      })
    },
    beforeAvatarUpload (file) {
      const isJPG = (file.type === 'image/jpeg') || (file.type === 'image/png') || (file.type === 'image/gif')
      const isLt1M = file.size / 1024 / 1024 < 1

      if (!isJPG) {
        this.$message.error(' JPG/PNG/GIF !')
        this.isCanUpload = false
        return false
      } else if (!isLt1M) {
        this.$message.error(' 1MB!')
        this.isCanUpload = false
        return false
      } else {
        getImgSize(file).then((img) => {
          this.isCanUpload = true
        })
      }
    },
    add () {
      this.pull = !this.pull
    },
    // 
    save () {
      if (this.itemCode === '' || this.itemCity === '' || this.itemZone === '' || this.jieName === '' || this.phone === '') {
        this.$message({
          message: '',
          type: 'warning'
        })
        return false
      } else if (!(/^1[34578]\d{9}$/.test(this.phone))) {
        this.$message({
          message: ',',
          type: 'warning'
        })
        return false
      } else {
        this.$message({
          message: '',
          type: 'success'
        })
        this.addArr.push({
          itemCode: this.itemCode,
          itemCity: this.itemCity,
          itemZone: this.itemZone,
          jieName: this.jieName,
          phone: this.phone,
          pCode: this.provincesCode,
          cCode: this.cityCode,
          zCode: this.zoneCode
        })
        this.jieName = ''
        this.phone = ''
      }
      this.addContent = true
      this.pull = false
    },
    // 
    addSure () {
      if (this.input === '' || this.input1 === '' || this.value === '' || this.addArr.length === 0 || this.input4 === '' || this.input5 === '' || this.input6 === '') {
        this.$message({
          message: ',...',
          type: 'warning'
        })
        return false
      }
      if (!(/^1[34578]\d{9}$/.test(this.input5))) {
        this.$message({
          message: ',',
          type: 'warning'
        })
        return false
      }
      let shopArr = []
      for (let i of this.addArr) {
        shopArr.push({
          province: i.itemCode,
          provinceCode: i.pCode,
          city: i.itemCity,
          cityCode: i.cCode,
          region: i.itemZone,
          regionCode: i.zCode,
          telephone: i.phone,
          address: i.jieName
        })
      }
      this.$ajax.post('/api/seller/shop/addShop', {
        sellerUserId: this.userInfo.sellerUserId,
        shopHomePage: this.input,
        shopName: this.input1,
        productClassId: this.valueCode,
        screenShot: this.imageUrl,
        postAddressList: JSON.stringify(shopArr),
        concatName: this.input4,
        concatTelephone: this.input5,
        concatQQ: this.input6,
        shopType: 0,
        productClassDetail: this.className
      }).then((data) => {
        let res = data.data
        if (res.code === '200') {
          this.$message({
            message: '',
            type: 'success'
          })
          // this.refresh()
          if (this.$route.query.toBindShop) {
            window.history.go(-1)
          } else {
            this.$router.push({ name: 'shopAdminList' })
          }
        } else {
          this.$message({
            message: data.data.message,
            type: 'warning'
          })
        }
      }).catch((err) => {
        console.log(err)
        this.$message.error('')
      })
    },
    // 
    refresh () {
      this.$ajax.post('/api/buyerAccount/refresh', {
        telephone: this.userInfo.telephone
      }).then((data) => {
        let res = data.data
        if (res.code === '200') {
          this.setUserInfo(res.data)
        } else {
          this.$message({
            message: data.data.message,
            type: 'warning'
          })
        }
      }).catch((err) => {
        console.log(err)
        this.$message.error('')
      })
    },
    // 
    shopFirst () {
      this.$ajax.post('/api/config/productClass/getJDFirstClass', {
      }).then((data) => {
        let res = data.data
        if (res.code === '200') {
          let arr = []
          for (let word of res.data) {
            let goods = {
              id: word.id,
              className: word.className
            }
            arr.push(goods)
          }
          this.shopType = arr
        } else {
          this.$message({
            message: data.data.message,
            type: 'warning'
          })
        }
      }).catch((err) => {
        console.log(err)
        this.$message.error('')
      })
    },
    // 
    provinceChange () {
      this.getCity()
      this.provincesCode = this.itemCode.code
      this.itemCode = this.itemCode.name
      this.itemCity = null
      this.itemZone = null
    },
    cityChange () {
      this.getZone()
      this.cityCode = this.itemCity.code
      this.itemCity = this.itemCity.name
      this.itemZone = null
    },
    zoneChange () {
      this.zoneCode = this.itemZone.code
      this.itemZone = this.itemZone.name
    },
    // 
    valueChange () {
      this.valueCode = this.value.id
      this.className = this.value.className
      this.value = this.value.className
    },
    // 
    Provinces () {
      this.$ajax.post('/api/config/location/getProvinceList', {
      }).then((data) => {
        let res = data.data
        if (res.code === '200') {
          let arr = []
          for (let word of res.data) {
            let goods = {
              id: word.id,
              name: word.name,
              code: word.code
            }
            arr.push(goods)
          }
          this.provinces = arr
        } else {
          this.$message({
            message: data.data.message,
            type: 'warning'
          })
        }
      }).catch((err) => {
        console.log(err)
        this.$message.error('')
      })
    },
    // 
    getCity () {
      this.$ajax.post('/api/config/location/getCityListByProvinceCode', {
        provinceCode: this.itemCode.code
      }).then((data) => {
        let res = data.data
        if (res.code === '200') {
          let arr = []
          for (let word of res.data) {
            let goods = {
              id: word.id,
              name: word.name,
              code: word.code,
              provinceCode: word.provinceCode
            }
            arr.push(goods)
          }
          this.city = arr
        } else {
          this.$message({
            message: data.data.message,
            type: 'warning'
          })
        }
      }).catch((err) => {
        console.log(err)
        this.$message.error('')
      })
    },
    // 
    getZone () {
      this.$ajax.post('/api/config/location/getAreaListByCityCode', {
        cityCode: this.itemCity.code
      }).then((data) => {
        let res = data.data
        if (res.code === '200') {
          let arr = []
          for (let word of res.data) {
            let goods = {
              id: word.id,
              name: word.name,
              code: word.code,
              cityCode: word.cityCode
            }
            arr.push(goods)
          }
          this.zone = arr
        } else {
          this.$message({
            message: data.data.message,
            type: 'warning'
          })
        }
      }).catch((err) => {
        console.log(err)
        this.$message.error('')
      })
    },
    remove (index) {
      this.addArr.splice(index, 1)
    },
    ...mapActions([
      'setUserInfo'
    ])
  }
}
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
.wrap
  padding 0 20px 0 20px
  .shop
    background #fff
    height 36px
    line-height 36px
    padding-left 20px
    font-size 14px
    color rgba(60, 60, 60, 1)
    .gray
      color rgba(137, 137, 137, 1)
  .content
    background #fff
    margin-top 24px
    padding-left 20px
    padding-right 20px
    h3
      padding-top 40px
      font-size 16px
      color rgba(68, 68, 68, 1)
    .line
      height 1px
      background rgba(222, 222, 222, 1)
      margin-top 16px
    .content_info
      margin-top 10px
      font-size 14px
      color rgba(68, 68, 68, 1)
      text-align center
      margin-top 20px
      min-width 800px
      max-width 1100px
      .site
        margin-left -28px
      .shopName
        margin-top 20px
      .wangwang
        margin-top 20px
        margin-left -11px
      .type
        margin-top 20px
        margin-left -22px
      .shopInfo
        display inline
        width 164px
        height 36px
        // border 1px solid rgba(255, 51, 65, 1)
        color white
        padding 10px 40px
        margin-left 12px
      .pic
        margin-top 20px
        .pic_admin
          margin-left -418px
          margin-top 13px
          display inherit
        .avatar-uploader
          border 1px solid #d9d9d9
          cursor pointer
          width 180px
          height 180px
          background rgba(250, 250, 250, 1)
          display inline-block
          margin-top -14px
          margin-left -127px
        .el-icon-plus
          font-size 48px
          line-height 180px
      .addAddress
        margin-top 25px
        margin-left -300px
      .pullDown
        width 480px
        margin-top 20px
        padding 40px
        display inline-block
        box-shadow 0 1px 5px rgba(0, 0, 0, 0.2)
        margin-left 80px
      .person
        margin-top 32px
        margin-left -80%
        font-size 14px
        color rgba(68, 68, 68, 1)
      .personInfo
        margin-top 60px
        padding-bottom 60px
      .addContent
        width 480px
        display inline-block
        margin-top 10px
        padding 40px
        text-align left
</style>


