import axios from 'axios'
import md5 from 'md5'

// 
export const uploadPromise = new Promise((resolve, reject) => {
  axios.post('/api/config/sts/getStsParam', {}).then((response) => {
    resolve(response)
  }).catch((error) => {
    console.log(error)
    reject(error)
  })
})

// 
export function uploadFile (res, file) {
  let name = file.lastModified + md5(file.name) + file.name
  const config = {
    endpoint: 'http://bj.bcebos.com', // Bucket
    credentials: {
      ak: res.accessKeyId, // AccessKey
      sk: res.secretAccessKey // SecretAccessKey
    },
    sessionToken: res.sessionToken
      // STSsessionToken
  }

  /* global baidubce */
  /* eslint no-undef: "error" */
  let client = new baidubce.sdk.BosClient(config)
  return new Promise((resolve, reject) => {
    client.putObjectFromBlob('scalp', name, file).then((response) => {
      var url = client.generatePresignedUrl('scalp', name)
      var urlForm = url.split('?')[0]
      resolve(urlForm)
    }).catch((error) => {
      reject(error)
    })
  })
}

// 
export function getImgSize (file) {
  return new Promise((resolve, reject) => {
    var reader = new FileReader()
    reader.onload = (theFile) => {
      var image = new Image()
      image.onload = () => {
        resolve(image)
      }
      image.src = theFile.target.result
    }
    reader.readAsDataURL(file)
  })
}



